		addLeaf() {
			this.newpname = ""
			this.addleaf = true
		},
		editLeafSubmit() {
			var p
			for (var i = 1; i < this.nowp.treeAncestors.length; i++) {
				console.log(this.nowp.treeAncestors[i].name)
				//第一次，引用
				if (i == 1) {
					p = this.treeData.children
					continue
				}
				//最后一次，修改值
				if (i == this.nowp.treeAncestors.length - 1) {
					for (var j = 0; j < p.length; j++) {
						if (p[j].name == this.nowp.treeAncestors[i].name) {
							p[j].name = this.nowpname
							console.log(this.treeData)
							this.initChart()
							break
						}
					}
				}
				for (var j = 0; j < p.length; j++) {
					if (p[j].name == this.nowp.treeAncestors[i].name) {
						p = p[j].children
					}
				}
			}
			this.editleaf = false
			this.leafinput = false

		},
		addLeafSubmit() {
			var p
			for (var i = 1; i < this.nowp.treeAncestors.length; i++) {
				console.log(this.nowp.treeAncestors[i].name)
				//第一次，引用
				if (i == 1) {
					p = this.treeData.children
					continue
				}
				//最后一次，添加子节点
				if (i == this.nowp.treeAncestors.length - 1) {
					for (var j = 0; j < p.length; j++) {
						if (p[j].name == this.nowp.treeAncestors[i].name) {
							if (p[j].children != null) {
								p[j].children.push(
									{
										name: this.newpname,
										children: []
									}
								)
							} else {
								p[j].children =
									[{
										name: this.newpname,
										children: []
									}]

							}
							this.initChart()
							break
						}
					}
				}
				for (var j = 0; j < p.length; j++) {
					if (p[j].name == this.nowp.treeAncestors[i].name) {
						p = p[j].children
					}
				}
			}
			this.addleaf = false
			this.leafinput = false
		},
		deleteLeaf() {
			this.$confirm('此操作将永久删除该节点及其子节点, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				var p
				for (var i = 1; i < this.nowp.treeAncestors.length; i++) {
					console.log(this.nowp.treeAncestors[i].name)
					//第一次，引用
					if (i == 1) {
						p = this.treeData.children
						continue
					}
					//最后一次，删除自身
					if (i == this.nowp.treeAncestors.length - 1) {
						for (var j = 0; j < p.length; j++) {
							if (p[j].name == this.nowp.treeAncestors[i].name) {
								p = p.splice(j, 1)
								this.initChart()
								this.leafinput = false
								this.$message({
									message: '删除成功！',
									type: 'success'
								});
								break
							}
						}
					}
					for (var j = 0; j < p.length; j++) {
						if (p[j].name == this.nowp.treeAncestors[i].name) {
							p = p[j].children
						}
					}
